import React, { useState, useEffect } from "react";
import Moralis from "../moralis";
import {
  TextareaAutosize,
  Stepper,
  Step,
  StepLabel,
  Button,
  Select,
  MenuItem,
} from "@mui/material";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableFooter,
  TableRow,
  Paper,
  Stack,
  InputLabel,
  FormControl,
} from "@mui/material";
import * as XLSX from "xlsx";
import {
  sendEthBulk,
  Ethsend,
  sendTokenbulk,
  TokenSend,
  estimateEthBulk,
  estimateEthSend,
  estimateTokenBulk,
  estimateTokenSend,
  AproveSender,
} from "../blockchain/functions";
const {
  toChecksumAddress,
  checkAddressChecksum,
} = require("ethereum-checksum-address");

function BatchBNB() {
  const [isSending, setIsSending] = useState(false);
  const [_to, set_to] = useState([]);
  const [_amount, set_amount] = useState([]);
  const [tokenToSend, setTokenToSend] = useState("");
  const [errorMSG, setErrorMsg] = useState("");
  const [textArea, setTextArea] = useState();
  const [userBalances, setUserBalances] = useState([]);
  const [transactionCost, setTransactionCost] = useState();
  const [transactionType, setTransactionType] = useState();
  const [balanceError, setBalanceError] = useState("");
  const [selectedTokenBalance, setSelectedTokenBalance] = useState("");
  const [isAllowed, setIsAllowed] = useState(false);
  const [userAddress, setUserAddress] = useState("");
  const steps = ["Prepare", "Confirm", "Send"];
  const [activeStep, setActiveStep] = useState(0);

  const processData = (dataString) => {
    const dataStringLines = dataString.split(/\r\n|\n/);
    const headers = dataStringLines[0].split(
      /,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/
    );

    const list = [];
    for (let i = 1; i < dataStringLines.length; i++) {
      const row = dataStringLines[i].split(
        /,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/
      );
      if (headers && row.length === headers.length) {
        const obj = {};
        for (let j = 0; j < headers.length; j++) {
          let d = row[j];
          if (d.length > 0) {
            if (d[0] === '"') d = d.substring(1, d.length - 1);
            if (d[d.length - 1] === '"') d = d.substring(d.length - 2, 1);
          }
          if (headers[j]) {
            obj[headers[j]] = d;
          }
        }

        if (Object.values(obj).filter((x) => x).length > 0) {
          list.push(obj);
        }
      }
    }

    const columns = headers.map((c) => ({
      name: c,
      selector: c,
    }));
    console.log("list", list, "columns", columns);
    let addresses = [];
    let amounts = [];
    let result = [];
    list.map((el) => {
      result.push(el.Address);
      result.push(el.Amount + "\n");
      addresses.push(el.Address);
      amounts.push(el.Amount);
    });
    console.log(amounts, addresses);
    console.log(result);

    setTextArea(result.join());
    checkInputs(result);

    set_amount(amounts);

    set_to(addresses);
  };

  function handleFile(e) {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = (evt) => {
      /* Parse data */
      const bstr = evt.target.result;
      const wb = XLSX.read(bstr, { type: "binary" });
      /* Get first worksheet */
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      /* Convert array of arrays */
      const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
      processData(data);
    };
    reader.readAsBinaryString(file);
  }

  const handleText = (text) => {
    let result = text.split(" ").join("").split("\n").join("").split(",");
    checkInputs(result);
  };

  const checkInputs = (result) => {
    let addresses = [];
    let amounts = [];
    let errormsg = "";
    let line = 0;
    for (var i = 0; i < result.length; i += 2) {
      line++;
      addresses.push(result[i]);
      result[i + 1] && amounts.push((result[i + 1] * 10 ** 18).toString());
      let checkAddress;
      try {
        checkAddress = toChecksumAddress(result[i]);
      } catch (error) {
        console.log(error);
      }

      if (!checkAddress) {
        console.log(`${result[i]} it's not a valid address`);

        errormsg = `line ${line}: ${result[i]} is a invalid wallet address and wrong amount. E.g:address,number`;
      } else if (!Number(result[i + 1])) {
        console.log(`${result[i + 1]} it's not a valid number`);
        errormsg = `line ${line}: ${result[i + 1]}  is a invalid wrong amount`;
      }
    }
    setErrorMsg(errormsg);
    set_to(addresses);
    set_amount(amounts);
  };

  const nextStep = () => {
    setActiveStep(activeStep + 1);
  };

  const prevStep = () => {
    setActiveStep(activeStep - 1);
  };

  const checkAllowance = async () => {
    if (tokenToSend !== "BNB") {
      console.log(tokenToSend, "token");
      try {
        const options = {
          chain: "bsc testnet",

          owner_address: window.ethereum.selectedAddress,
          spender_address: "0x3457EbBE22064f0aC5a1C46Fc00b326fB7013cA2",
          address: tokenToSend,
        };
        const allowance = await Moralis.Web3API.token.getTokenAllowance(
          options
        );
        if (Number(allowance.allowance) < 500000 * 10 ** 18) {
          setIsAllowed(false);
          console.log("no");
        } else {
          setIsAllowed(true);
          console.log("si");
        }
        console.log(allowance);
      } catch (error) {
        console.log(error);
      }
    }
  };

  const checkBalanceForTransfer = async () => {
    let total = totalAmount();

    let token = await userBalances.find((e) => e.token_address === tokenToSend);
    let decimals = token.decimals || 18;
    let balance = token.balance / 10 ** decimals;
    setSelectedTokenBalance(balance);
    console.log(balance, "balance", decimals);

    if (total > balance) {
      setBalanceError("Not Enough Balance to Transfer");
    } else if (!transactionCost) {
      setBalanceError(
        "Can't calculate transaction cost, transaction may fail, please verify if the token is approved for transfer"
      );
    } else {
      setBalanceError("");
    }

    return balance;
  };

  const removeRecipient = (index) => {
    const new_to = _to.filter((el, key) => {
      return key !== index;
    });
    const new_amount = _amount.filter((el, key) => {
      return key !== index;
    });

    set_to(new_to);
    set_amount(new_amount);
  };

  const totalAmount = () => {
    let total = 0;
    _amount.map((el) => (total = total + Number(el) / 10 ** 18));
    return total;
  };

  const checkDecimals = async () => {
    try {
      let token = await userBalances.find(
        (e) => e.token_address === tokenToSend
      );
      let decimals = token.decimals || 18;

      if (decimals === 18) {
        console.log("decimals18");
        return _amount;
      } else {
        console.log("decimals", decimals);
        let amount = [];
        _amount.map((el) => {
          let temp = el / 10 ** 18;
          amount.push(temp * 10 ** decimals);
        });
        return amount;
      }
    } catch (error) {
      console.log(error);
    }
  };

  const costOperation = async () => {
    let cost;

    let amount = await checkDecimals();

    if (transactionType === "sendETH") {
      cost = await estimateEthSend(_to, amount);

      console.log("eth send");
    } else if (transactionType === "bulkETH") {
      cost = await estimateEthBulk(_to, amount);

      console.log("eth bulk");
    } else if (transactionType === "sendToken") {
      cost = await estimateTokenSend(tokenToSend, _to, amount);

      console.log("token send");
    } else if (transactionType === "bulkToken") {
      cost = await estimateTokenBulk(tokenToSend, _to, amount);

      console.log("token bulk");
    }
    if (cost) {
      setTransactionCost(cost / 10 ** 18 + " BNB");
    } else {
      setTransactionCost("can't calculate transaction cost");
    }
  };

  const sendTransaction = async () => {
    setIsSending(true);

    let amount = await checkDecimals();

    let receipt;
    if (transactionType === "sendETH") {
      console.log("eth send");
      receipt = await Ethsend(_to, amount);
    } else if (transactionType === "bulkETH") {
      console.log("eth bulk");
      receipt = await sendEthBulk(_to, amount);
      console.log(receipt);
    } else if (transactionType === "sendToken") {
      console.log("token send");
      receipt = await TokenSend(tokenToSend, _to, amount);
      console.log(receipt);
    } else if (transactionType === "bulkToken") {
      console.log("token bulk");
      receipt = await sendTokenbulk(tokenToSend, _to, amount);
      console.log(receipt);
    }
    setIsSending(false);
  };

  const login = async () => {
    await Moralis.authenticate().then(function (user) {
      setUserAddress(user.get("ethAddress"));
      getBalances();
    });
  };

  const getBalances = async () => {
    if (window.ethereum) {
      try {
        const options = {
          chain: "bsc testnet",
          address: window.ethereum.selectedAddress,
        };
        const balances = await Moralis.Web3API.account.getTokenBalances(
          options
        );
        const nativeBalance = await Moralis.Web3API.account.getNativeBalance(
          options
        );
        balances.unshift({
          name: "BNB",
          token_address: "BNB",
          balance: nativeBalance.balance,
        });
        setUserBalances(balances);
        console.log(balances);
      } catch (error) {
        console.log("error", error);
      }
    }
  };

  const sameAmount = () => {
    let sameAmount = true;
    let amountToCompare = _amount[0];
    _amount.map((i) => {
      if (i !== amountToCompare) {
        sameAmount = false;
      }
    });
    return sameAmount;
  };

  const checkTransactionType = () => {
    let isSameAmount = sameAmount();

    if (tokenToSend === "BNB") {
      if (isSameAmount) {
        console.log("bnb same amount");
        setTransactionType("sendETH");
      } else {
        console.log("bnb different amount");
        setTransactionType("bulkETH");
      }
    } else {
      if (isSameAmount) {
        console.log("token same amount");
        setTransactionType("sendToken");
      } else {
        console.log("token different amount");
        setTransactionType("bulkToken");
      }
    }
  };

  useEffect(() => {
    getBalances();
  }, []);

  useEffect(() => {
    costOperation();
  }, [activeStep]);

  // useEffect(() => {
  //   checkAllowance();
  // }, [tokenToSend]);

  return (
    <div>
      {userAddress ? (
        <h4>
          <Button onClick={() => login()}>
            {userAddress?.substring(0, 10)}...{userAddress?.substring(36, 42)}{" "}
          </Button>
        </h4>
      ) : (
        <Button onClick={() => login()}> login </Button>
      )}

      <h2>MultiSender</h2>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      {activeStep === 0 && (
        <>
          {" "}
          <br />
          <h3>Token</h3>
          <FormControl>
            <InputLabel>Select a Token</InputLabel>
            <Select
              value="Token"
              style={{ width: 600 }}
              label="Select a Token"
              value={tokenToSend}
              onChange={(e) => {
                setTokenToSend(e.target.value);
                checkAllowance();
              }}
            >
              {userBalances.map((token) => {
                const { token_address, name } = token;

                return (
                  <MenuItem key={token_address} value={token_address}>
                    {name} - {token_address}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
          <br />
          <h3>Addresses with Amounts</h3>
          <input type="file" onChange={(e) => handleFile(e)} />
          <br />
          <TextareaAutosize
            aria-label="minimum height"
            value={textArea}
            onChange={(e) => {
              setTextArea(e.target.value);
              handleText(e.target.value);
            }}
            onPaste={(e) => {
              setTextArea(e.target.value);
              handleText(e.target.value);
            }}
            setTextArea
            minRows={4}
            placeholder="Insert Address and Amount separated by comma"
            style={{ width: 600, "min-width": "200px" }}
          />
          <h6>The address and amount are separated by commas</h6>
          {errorMSG && <h3>{errorMSG}</h3>}
          <br />
          <Button
            disabled={tokenToSend === "" || errorMSG ? 1 : 0}
            // disabled={!isAllowed ? 1 : 0}
            onClick={() => {
              nextStep();
              checkTransactionType();
              checkBalanceForTransfer();
            }}
            variant="contained"
          >
            next
          </Button>
          <br />
          <br />
          <Button variant="contained" onClick={() => AproveSender(tokenToSend)}>
            Aprove Token!
          </Button>
        </>
      )}
      {activeStep === 1 && (
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
          spacing={1}
        >
          <h3>List of Recipients</h3>{" "}
          <TableContainer sx={{ width: 650 }} component={Paper}>
            <Table sx={{ width: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">Address</TableCell>
                  <TableCell align="left">Amount</TableCell>
                  <TableCell align="center"></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {_to.map((address, index) => (
                  <TableRow
                    key={index}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell align="left">{address}</TableCell>
                    <TableCell align="left">
                      {_amount[index] / 10 ** 18}
                    </TableCell>
                    <TableCell align="center">
                      <Button onClick={() => removeRecipient(index)}>
                        {" "}
                        remove
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
              <TableHead>
                <TableRow>
                  <TableCell align="left">
                    Total Recipients: {_to.length}
                  </TableCell>
                  <TableCell align="left">
                    Total Amount: {totalAmount()}
                  </TableCell>
                </TableRow>
              </TableHead>
            </Table>
          </TableContainer>
          <br />
          <TableContainer sx={{ width: 650 }} component={Paper}>
            <Table sx={{ width: 650 }} aria-label="simple table">
              <TableBody>
                <TableRow key={"a"}>
                  <TableCell align="left">
                    Total Number of Addresses: {_to.length}
                  </TableCell>
                  <TableCell align="left">
                    Total Number of Tokens to be Sent: {totalAmount()}{" "}
                    {
                      userBalances.find((e) => e.token_address === tokenToSend)
                        .symbol
                    }
                  </TableCell>
                </TableRow>
                <TableRow key={"b"}>
                  <TableCell align="left">
                    Number of Transactions Needed: 1
                  </TableCell>
                  <TableCell align="left">
                    Your Token Balance: {selectedTokenBalance}
                    {
                      userBalances.find((e) => e.token_address === tokenToSend)
                        .symbol
                    }
                  </TableCell>
                </TableRow>
                <TableRow key={"c"}>
                  <TableCell align="left">
                    Approximate Cost Operation: {transactionCost}
                  </TableCell>
                  <TableCell align="left">
                    Your BNB Balance {userBalances[0].balance / 10 ** 18} BNB
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
          <br />
          {balanceError && <h3>{balanceError}</h3>}
          <br />
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={2}
          >
            <Button onClick={() => prevStep()} variant="contained">
              prev
            </Button>

            <Button
              disabled={balanceError ? 1 : 0}
              onClick={() => nextStep()}
              variant="contained"
            >
              next
            </Button>
          </Stack>
        </Stack>
      )}
      {activeStep === 2 && (
        <>
          <Button
            disabled={isSending ? 1 : 0}
            onClick={() => sendTransaction()}
            variant="contained"
          >
            {isSending ? "Sending..." : "Send Transaction"}
          </Button>
          <br />
          <br />
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={2}
          >
            <Button onClick={() => prevStep()} variant="contained">
              prev
            </Button>
            <Button
              onClick={() => sendEthBulk(_to, _amount)}
              variant="contained"
            >
              Send
            </Button>
          </Stack>
        </>
      )}
    </div>
  );
}

export default BatchBNB;
