import web3 from "./web3";
import senderInterface, { senderAddress } from "./MultiSenderInterface";
import ERC20Interface from "./ERC20Interface";

export const sendEthBulk = async (_to, _amount) => {
  try {
    let value = 10000000000000000;
    _amount.map((el) => {
      value = value + Number(el);
    });
    let instance = await senderInterface();
    console.log(value, _to, _amount);
    let receipt = await instance.methods
      .bulksendEth(_to, _amount)
      .send({ value: value.toString(), from: window.ethereum.selectedAddress });

    console.log(receipt);
    return receipt;
  } catch (error) {
    console.log(error);
  }
};

export const estimateEthBulk = async (_to, _amount) => {
  try {
    let value = 10000000000000000;
    _amount.map((el) => {
      value = value + Number(el);
    });
    let instance = await senderInterface();
    let gasPrice = await web3.eth.getGasPrice();
    console.log(value, _to, _amount);
    let receipt = await instance.methods.bulksendEth(_to, _amount).estimateGas({
      value: value.toString(),
      from: window.ethereum.selectedAddress,
    });

    return receipt * gasPrice;
  } catch (error) {
    console.log(error);
  }
};

export const Ethsend = async (_to, _amount) => {
  try {
    let value = Number(_amount[0]) * _to.length + 10000000000000000;
    console.log(_to, _amount, value);
    let instance = await senderInterface();
    let receipt = await instance.methods
      .sendEth(_to, _amount[0])
      .send({ value: value.toString(), from: window.ethereum.selectedAddress });

    console.log(receipt);
  } catch (error) {
    console.log(error);
  }
};

